.recent-orders {
    width: 90%;
    margin: 20px 0;
    padding: 20px;
    background-color: #615b5b;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    color: white;
}

.recent-orders h2 {
    margin-bottom: 20px;
    color: white;
}

.recent-orders table {
    width: 100%;
    border-collapse: collapse;
}

.recent-orders th, .recent-orders td {
    padding: 10px;
    border: 1px solid #ddd;
    text-align: left;
}

.recent-orders th {
    background-color:  #615b5b;
}

.recent-orders tr:nth-child(even) {
    background-color:  #615b5b;
}

@media (max-width: 768px) {
    .recent-orders {
        padding: 10px;
    }

    .recent-orders th, .recent-orders td {
        padding: 5px;
    }
}
