.header {
    width: calc(100% - 60px);
    height: 60px;
    background-color: #615b5b;
    color: white;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    left: 60px;
}

.search_bar {
    display: flex;
    align-items: center;
}

.search_bar input {
    margin-left: 10px;
    padding: 5px;
}

.header nav ul {
    list-style-type: none;
    display: flex;
    margin: 0;
    padding: 0;
}

.header nav ul li {
    margin-left: 20px;
    cursor: pointer;
}

@media (max-width: 768px) {
    .header {
        width: calc(100% - 50px);
        left: 50px;
    }

    .search_bar input {
        width: 100px;
    }

    .header nav ul li {
        margin-left: 10px;
    }
}
