.activity-graph {
    width: 90%;
    height: 300px;
    margin: 20px 0;
    padding: 20px;
    background-color: #615b5b;
    border-radius: 8px;
    text-align: center;
    color: white;
}

.activity-graph h2 {
    margin-bottom: 20px;
}

@media (max-width: 768px) {
    .activity-graph {
        height: 200px;
        padding: 10px;
    }

    .activity-graph h2 {
        font-size: 1.2em;
    }
}
