.sidebar {
    width: 60px;
    height: 100vh;
    background-color: #615b5b;
    color: #fff;
    position: fixed;
    top: 0;
    left: 0;
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebar ul {
    list-style-type: none;
    padding: 0;
}

.sidebar ul li {
    margin-bottom: 20px;
}

.log-out {
    margin-top: auto;
    margin-bottom: 20px;
}

@media (max-width: 768px) {
    .sidebar {
        width: 50px;
    }

    .sidebar ul li img {
        width: 30px;
    }

    .log-out {
        width: 30px;
    }
}





